.detail-view {
    width: 50%;
    height: 100%;
    padding: 10px;
    background-color: rgba(201, 110, 236, 0.178);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
  }
  
  .sprite-image {
    width: 80%;
    height: 150px;
    margin: 10px auto;
    background-color: #5858583a;
    border: 5px solid rgba(108, 68, 124, 0.658);
    border-radius: 10px;
    color: #0a0a0ae8;
    text-align: center;
  }
  
  .data-wrapper {
    width: 80%;
    height: 110px;
    padding: 10px;
    margin: 0 auto;
    background-color: #5858584f;
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 20px rgba(75, 74, 74, 0.22);
            box-shadow: inset 0 0 20px rgba(75, 74, 74, 0.22);
    color: #FFF;
    text-shadow: 1px 1px #79267434;
  }
  
  .data-name {
    margin: 0 0 5px;
  }
  
  .data-char {
    font-size: 12px;
  }

  .data-tile {
      text-align:center;
      color: white;
      text-shadow: 2px 1px #79267434;
  }


